    .modal {
        position: fixed;
        top: 0;
        left: 0;
        width:100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.6);
    }
    
    .modal-main {
        position:fixed;
        background: white;
        width: 35%;
        height: 40%;
        top:50%;
        left:50%;
        border: 2px solid black;
        -webkit-transform: translate(-50%,-50%);
                transform: translate(-50%,-50%);
    }
    
    .display-block {
        display: block;
    }
    
    .display-none {
        display: none;
    }

    .close-button {
        float: right;
        font-size:  25px;
    }

.slideshow-container {
  max-width: 1000px;
  position: relative;
  margin: auto;
  border : 1px solid black
}

.prev, .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  padding: 16px;
  margin-top: -22px;
  color:black;
  font-size: 18px;
}

.prev {
  left: 0;
  border-radius: 3px 0 0 3px;
}
.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}
.text {
  color:black;
  /* position: absolute; */
  bottom: 80px;
  width: 100%;
  font-size: 163px;
}



